main {
  margin: 0 auto;
  width: 960px;
  border: 5px solid #0b1c07;
}

.board {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
}

.cell {
  width: 10px;
  height: 10px;
  -ms-flex: 0 1 auto;
      flex: 0 1 auto;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: 1px solid #0b1c07;
}
.cell.dead {
  background-color: rgba(14, 36, 8, 0.9);
}
.cell.live {
  background-color: #217d0b;
}
.cell.birth {
  background-color: #a5f591;
}

.controls {
  width: 100%;
  margin: 0;
  padding: 16px 0;
  border-top: 5px solid #0b1c07;
  background-color: #0b1c07;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: baseline;
      align-items: baseline;
}

.buttons, .generations {
  margin: 0;
  padding: 0;
  width: 50%;
}

.buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: baseline;
      align-items: baseline;
}

button {
  font-family: "Spectral", "Palatino", "Times New Roman", "Times", serif;
  margin: 0;
  padding: 5px;
  -ms-flex: 0 1 auto;
      flex: 0 1 auto;
  color: #0b1c07;
  background-color: #f3fcf1;
}
button:active {
  background-color: #e2f7dd;
}

.generations h2 {
  margin: 0;
  padding: 0;
  color: #f2feef;
}



