.App {
  font-family: "Spectral", "Palatino", "Times New Roman", "Times", serif;
  text-align: center;
}

h1 {
  color: #154e07;
}

.subtitle {
  font-style: italic;
}
.subtitle a {
  text-decoration: none;
}

a, a:link, a:visited {
  color: #1064ad;
}

a:hover, a:active {
  color: #0c497e;
}



